import React, {Fragment, useState} from 'react';
import Back from "../images/back.svg";
import User from "../images/dentist.svg";
import {Link} from "react-router-dom";
import {productService} from "./ProductSerivces";

export function  Register (){

    const [state,setSatate]=useState({register:{first_name:'' , last_name:'',fix_phone:'',address:'',phone:localStorage.getItem('phone')} , errors:{author:[],text:[]} });


    const submitHandler = event => {
        event.preventDefault();
        // console.log(event)
        if (formDataIsValid()){
            document.getElementById('send_btn').setAttribute("disabled",true);

            // console.log(state.register)
            send_request(state.register);
        }
    }

    const formDataIsValid= () =>{
        const errors= {};
        if (!state.register.first_name){
            errors.first_name='این فیلد اجباریست';
        }if (!state.register.last_name){
            errors.last_name='این فیلد اجباریست';
        }if (!state.register.address){
            errors.address='این فیلد اجباریست';
        }if (!state.register.fix_phone){
            errors.fix_phone='این فیلد اجباریست';
        }

        setSatate({...state,errors});
        return !(errors.fix_phone || errors.address ||errors.last_name ||errors.first_name );
        // return true
    }

    const changeHandler = event => {
        const formData = {[event.target.name]: event.target.value };
        // console.log(formData)
        setSatate({...state,register: {...state.register, ...formData}})

        // console.log(state)
    }

    return <Fragment>

        <div className="login_image" style={{marginTop:'5%'}}>
            <img style={{width:"100%",height:200}} src={User} />

            <div style={{marginTop:'10%',width:'100%'}}>
                <form onSubmit={submitHandler}>
                    <div className="row">
                        <div className="col-12">
                            مشخصات خود را وارد کنید

                        </div>
                    </div>
                    <div className="row" style={{marginTop:20}}>
                        <div className="col-12">
                            <input className="form-control text-center"  onChange={changeHandler} name="first_name" type="text"  placeholder="نام"/>
                            <div id="emailHelp" className="form-text text-danger">{state.errors.first_name || []}</div>

                        </div>
                        <div className="col-12" style={{marginTop:20}}>
                            <input className="form-control text-center" onChange={changeHandler} name="last_name" type="text"  placeholder="نام خانوادگی"/>
                            <div id="emailHelp" className="form-text text-danger">{state.errors.last_name || []}</div>

                        </div>
                        <div className="col-12" style={{marginTop:20}}>
                            <input className="form-control text-center" onChange={changeHandler} name="fix_phone" type="text"  placeholder="شماره تلفن ثابت"/>
                            <div id="emailHelp" className="form-text text-danger">{state.errors.fix_phone || []}</div>

                        </div>
                        <div className="col-12" style={{marginTop:20}}>
                            <textarea className="form-control text-center" onChange={changeHandler} name="address"  placeholder="آدرس مطب"/>
                            <div id="emailHelp" className="form-text text-danger">{state.errors.address || []}</div>

                        </div>

                        <div className="col-12" style={{marginTop:20}}>
                            <button id="send_btn" type="submit" style={{width:'100%',color:"white"}} className="btn btn-info" >ثبت نام</button>
                        </div>
                    </div>

                </form>

            </div>

        </div>
    </Fragment>

}

export async function send_request(values){
    productService.userRegister(values).then((response) => {
        if (response.data.status === 'ok'){
            localStorage.setItem('user_id',response.data.user_id);
            alert('ثبت نام شما با موفقیت انجام شد')
            window.open('/webapp','_self');
        }else {
            alert('مشکلی پیش آمد.');
        }
    });

}
