import axios from 'axios';

const apiAddress = "https://zobs.ir/zoobs/api/";
// const apiAddress = "http://localhost:8080/zoobs/public_html/zoobs/api/";
const mikhay ='T4>`YH;r3g-Kd<d:rpC_Fpxt3yy3P^uZgl`hjhCBcDM-tn<=}nByt@)B`D.v*HCCDE6F93E175B3EB8697EB3426FFA9';
var user_id = localStorage.getItem('user_id');
// var user_id = 'b9';

export const productService= {
    getProducts: () =>{
        return axios.get(apiAddress)
    },
    getProductById: (id) =>{
        return axios.get(apiAddress + id)
    },
    addComment: (productId,comment) =>{
        return axios.post(apiAddress+productId + "/comments",comment)
    },
    getCategory: (catId,userId) => {

        return  axios({
            method: 'post',
            url: apiAddress + "category",
            data: {
                action: 'childs',
                mikhay: mikhay,
                parent_id: catId,
                user_id: userId
            }
        });

    },getCategoryPrice: (catId) => {
        return  axios({
            method: 'post',
            url: apiAddress + "category",
            data: {
                action: 'get_price',
                id: catId,
            }
        });
    },getPriceOff: (code) => {
        return  axios({
            method: 'post',
            url: apiAddress + "price_off_code",
            data: {
                mikhay: mikhay,
                code: code,
            }
        });
    },addService: (code,address,catId) => {

        return  axios({
            method: 'post',
            url: apiAddress + "add_service",
            data: {
                mikhay: mikhay,
                code: code,
                address: address,
                cat_id: catId,
                user_id:user_id
            }
        });
    },getallServices: (page) => {

        return  axios({
            method: 'post',
            url: apiAddress + "get_all_services",
            data: {
                mikhay: mikhay,
                user_id:user_id,
                page: page
            }
        });
    },getServices: (id) => {

        return  axios({
            method: 'post',
            url: apiAddress + "get_services",
            data: {
                mikhay: mikhay,
                user_id:user_id,
                id: id
            }
        });
    },getChat: () => {

        return  axios({
            method: 'post',
            url: apiAddress + "get_tickets_web",
            data: {
                mikhay: mikhay,
                user_id:user_id
            }
        });
    },addChat: (desc) => {

        return  axios({
            method: 'post',
            url: apiAddress + "add_ticket",
            data: {
                mikhay: mikhay,
                user_id:user_id,
                description:desc
            }
        });
    },sendOtp: (phone) => {
        return  axios({
            method: 'post',
            url: apiAddress + "phone_otp",
            data: {
                mikhay: mikhay,
                action:'send_otp',
                phone:phone
            }
        });
    },verifyOtp: (phone,otp) => {
        return  axios({
            method: 'post',
            url: apiAddress + "phone_otp",
            data: {
                mikhay: mikhay,
                action:'verify_otp',
                phone:phone,
                otp:otp,
            }
        });
    },searchCategory: (name) => {
        return  axios({
            method: 'post',
            url: apiAddress + "category",
            data: {
                mikhay: mikhay,
                action:'search',
                user_id:user_id,
                title:name,
            }
        });
    },getUserInfo: () => {
        return  axios({
            method: 'post',
            url: apiAddress + "get_user_info",
            data: {
                mikhay: mikhay,
                user_id:user_id
            }
        },setTimeout(20));
    },userRegister: (data) => {
        data.mikhay=mikhay;
        return  axios({
            method: 'post',
            url: apiAddress + "register",
            data: data
        });
    },userUpdate: (data) => {
        data.mikhay=mikhay;
        data.user_id=user_id;
        return  axios({
            method: 'post',
            url: apiAddress + "dashboard",
            data: data
        });
    },getSubscription: () => {
        return  axios({
            method: 'post',
            url: apiAddress + "subscription",
            data: {
                mikhay: mikhay,
            }
        });
    }
}
