import React,{Fragment} from 'react';
import {productService} from "./ProductSerivces";
import User from '../images/dentist.svg'

export function LoginPage(){

    async function  send_otp () {

        var phone = document.getElementById('phone').value;
        if (phone.length !== 11){
            alert('شماره همراه را به درستی وارد کنید.');
        }else {
            document.getElementById('send_btn').setAttribute("disabled",true);

            const response = await productService.sendOtp(phone);

            if (response.data.status === 'ok'){
                document.getElementById('send_otp').style.display="none";
                document.getElementById('verify_otp').style.display="block";

            }else {
                alert('در برقراری ارتباط با سرور مشکلی به وجود آمد.');
            }
        }
    }

    async function verify_otp(){
        var phone = document.getElementById('phone').value;
        var otp = document.getElementById('otp').value;



        if (otp.length !== 4){
            alert('کد را به درستی وارد کنید.');
        }else {
            // document.getElementById('verify_btn').setAttribute("disabled",true);
            document.getElementById('verify_btn').setAttribute("disabled",true);

            const response = await productService.verifyOtp(phone,otp);

            if (response.data.status === 'ok'){
                // alert(JSON.stringify(response.data))

                // alert(localStorage.getItem('user_id'))
                if (response.data.user.user_id === 0){
                    localStorage.setItem('phone',phone)
                    window.open('/webapp/register',"_self")
                }else {
                    localStorage.setItem('user_id',response.data.user.user_id)
                    window.open('/webapp',"_self")
                }

            }else {
                document.getElementById('verify_btn').removeAttribute("disabled");
                alert('در برقراری ارتباط با سرور مشکلی به وجود آمد.');
            }
        }
    }

    return <div style={{background:"red",width:'100%'}}>
        <div className="login_image">
            <img style={{width:"100%",height:200}} src={User} />

            <div id="send_otp" >

                <div className="login_content">
                    لطفا شماره همراه خود را وارد کنید
                </div>

                <input id="phone" placeholder="09XXXXXXXXX" type="number" maxLength="11" className="login_input"/>

                <div>
                    <a href="https://zobs.ir/zoobs/pages/policy" className="login_link">
                        حریم خصوصی
                    </a>
                    <a href="https://zobs.ir/zoobs/pages/law" className="login_link">
                        شرایط و قوانین
                    </a>
                </div>
                <button className="login_btn" id="send_btn"  onClick={send_otp}>ورود یا ثبت نام</button>

            </div>

            <div id="verify_otp" style={{display:"none"}}>
                <div className="login_content">
                    کد ارسال شده را وارد کنید:
                </div>

                <input id="otp" placeholder="XXXX" className="login_input" type="number" maxLength="4"/>
                <button className="login_btn" id="verify_btn" onClick={verify_otp}>احراز هویت</button>
            </div>

        </div>

    </div>
}