import React,{Fragment} from 'react';
import {productService} from "./ProductSerivces";
import {Loading} from "./loading";
import Back from "../images/back.svg";
import User from "../images/dental.svg";
import Edit from "../images/pencil.svg";
import {Link} from "react-router-dom";
import Lottie1 from "../animation/circle.json";
import {LottieAnimation} from "../components/lottie";

export class  UserInfo extends React.Component {
    state = {data: [],error:0};

    async componentDidMount() {
        var userId = localStorage.getItem('user_id');

        if (userId == null || userId == 0 ){
            alert('کاربر گرامی برای استفاده از این صفحه بایست ابتدا وارد شوید.')
            window.open('/webapp/login',"_self")
        }
        const {data} = await productService.getUserInfo();

        this.setState({data});
        //
        // await productService.getUserInfo().then(function (response) {
        //     alert('get')
        //     this.setState({data:{response},error:0});
        // }).catch(function (error) {
        //     alert('eroor')
        //     this.setState({ data:[] ,error:1});
        // });

        // console.log(this.state.data)

    }


    render() {
        var su='  اشتراک شما '
        su+=this.state.data.day
        su+=' روز دیگر  '
        var nosu='شما اشتراک ندارید'
        const data = this.state.data;
        if (data.length <1) {
            return <Loading />
        }
        else {
            return <Fragment>
                <div className="app_header" >
                    <div className="back_svg" onClick={back_history}>
                        <img width="100%" src={Back}/>
                    </div>
                    <div className="back_title">
                        اطلاعات
                    </div>
                </div>

                <div>
                    <div style={{height:70,width:'100%',float:"right"}}>

                    </div>
                    <div className="login_image">
                        <LottieAnimation lotti={Lottie1} height={100} width={100}>

                        </LottieAnimation>
                        <img style={{marginTop:"-35%",width:"10%"}} src={User}/>

                        <div style={{marginTop:'10%',width:'100%'}}>
                            <div className="row dashboard_item">
                                <div className="col-4 text-start">
                                    <Link to="/webapp/subscription" >
                                        <button className="btn btn-info" style={{fontSize:9,color:"white"}}>خرید اشتراک</button>
                                    </Link>
                                </div>

                                <div className="col-8 text-end" style={{fontSize:15 }}>

                                    {(this.state.data.day)? su:nosu}


                                </div>
                            </div>

                            <div className="row dashboard_item">
                                <div className="col-6 text-start" style={{fontSize:15,color:'#26C6DA' }}>
                                    {this.state.data.phone}
                                </div>
                                <div className="col-6 text-end" style={{fontSize:15 }}>
                                    شماره کاربری
                                </div>
                            </div>
                            <div className="row dashboard_item">
                                <div className="col-6 text-start" style={{fontSize:15,color:'#26C6DA' }}>
                                    {this.state.data.first_name}
                                </div>
                                <div className="col-6 text-end" style={{fontSize:15 }}>
                                    نام
                                </div>
                            </div>
                            <div className="row dashboard_item">
                                <div className="col-6 text-start" style={{fontSize:15,color:'#26C6DA' }}>
                                    {this.state.data.last_name}
                                </div>
                                <div className="col-6 text-end" style={{fontSize:15 }}>
                                    نام خانوادگی
                                </div>
                            </div>
                            <div className="row dashboard_item">
                                <div className="col-6 text-start" style={{fontSize:15,color:'#26C6DA' }}>
                                    {this.state.data.fix_phone}
                                </div>
                                <div className="col-6 text-end" style={{fontSize:15 }}>
                                    تلفن ثابت
                                </div>
                            </div>
                            <div className="row dashboard_item">

                                <div className="col-12 text-end" style={{fontSize:15 }}>
                                    آدرس
                                </div>
                                <div className="col-12 text-end" style={{fontSize:15,color:'#26C6DA' }}>
                                    {this.state.data.address}
                                </div>
                            </div>
                            <Link to={{
                                pathname: "/webapp/edit" ,
                                params:this.state.data
                            }}
                            >
                                <div className="row dashboard_item" style={{border:'2px solid #26C6DA'}}>


                                    <div className="col-6 text-start" style={{fontSize:15,color:'#26C6DA' }}>
                                        <img width="15%" height="20" src={Edit}/>
                                    </div>
                                    <div className="col-6 text-end" style={{fontSize:15 }}>
                                        ویرایش
                                    </div>
                                </div>
                            </Link>
                        </div>
                    </div>
                    <div style={{height:70,width:'100%',float:"right"}}>

                    </div>

                </div>

            </Fragment>
        }




    }
}
function back_history(){
    window.history.back();
}