import React, {Component, Fragment} from 'react';
import { CategoryList,SearchCategory } from "../components/categorys";
import {productService} from "./ProductSerivces";
import Search from "../images/search_orange.svg";
import Cat1 from "../images/1.png";
import Cat2 from "../images/2.png";
import Cat3 from "../images/3.png";
import Cat4 from "../images/4.png";
import Cat5 from "../images/5.png";
import Cat6 from "../images/6.png";
import Cat7 from "../images/7.png";
import Cat8 from "../images/8.png";
import Cat9 from "../images/9.png";
import { Helmet } from 'react-helmet'

import Logo from "../images/zoobs-logo-white.png";

const TITLE = 'Zobs'


export class HomePage extends Component {
    state = { category: [
            {
                'id':3,
                'name':'جرم گیر',
                'pic':Cat3
            },
            {
                'id':2,
                'name':'اینسترومنت',
                'pic':Cat2
            },
            {
                'id':1,
                'name':'یونیت',
                'pic':Cat1
            },
            {
                'id':6,
                'name':'اتوکلاو',
                'pic':Cat4
            },
            {
                'id':5,
                'name':'ساکشن',
                'pic':Cat5
            },
            {
                'id':4,
                'name':'کاسه کراشوار',
                'pic':Cat6
            },
            {
                'id':9,
                'name':'کمپرسور',
                'pic':Cat7
            },
            {
                'id':8,
                'name':'RVG',
                'pic':Cat8
            },
            {
                'id':7,
                'name':'رادیوگرافی',
                'pic':Cat9
            },

    ],search:[]};

    // async componentDidMount() {
    //     const response = await productService.getProducts();
    //     this.setState({ products: response.data });
    // }
    //
    // render() {
    //     return (
    //         <div className="row">
    //             <ProductList products={this.state.products} />
    //         </div>
    //     );
    // }

    async onInputChange(event){
        event.persist();
        var cat_name = event.target.value
        var userId = localStorage.getItem('user_id');

        if (userId == null || userId == 0 ){
            alert('کاربر گرامی برای استفاده از این صفحه بایست ابتدا وارد شوید.')
            window.open('/webapp/login',"_self")
        }
        if (event.target.value.length >0){
            const response = await productService.searchCategory(cat_name);

            console.log(response.data)

            if (response.data.list.length >0){
                this.setState({ ...this.state , search:response.data.list })
                document.getElementById('categorySearch').style.display="block";
            }else {
                document.getElementById('categorySearch').style.display="none";

            }
        }else {
            document.getElementById('categorySearch').style.display="none";
        }


    }

    render() {
        return (
            <Fragment>
                <Helmet>
                    <title>{ TITLE }</title>
                </Helmet>
                <div style={style.header}>
                    <div className="card-header" style={{border:"none",background:"none"}}>
                        <img style={{width:"30%"}} src={Logo}/>
                    </div>
                    <div className="row card-header" style={{border:"none",background:"none"}}>
                        <div className="row" style={style.search}>

                            <div className="col-10" style={style.searchBox}>
                                <input onChange={this.onInputChange.bind(this)} placeholder="جست و جو...." type="text" style={{width:'100%',border:"none",direction:"rtl",marginRight:"5%",float:"right"}} />
                            </div>
                            <div className="col-2" style={style.searchIcon}>
                                <img style={{width:"100%",marginTop:"30%"}} src={Search}/>
                            </div>
                        </div>

                        <div id="categorySearch" className="categorySearch">
                            <SearchCategory data={this.state.search} />
                        </div>

                    </div>
                </div>

                <div className="container">
                    <div className="row" style={{marginTop:10}}>
                        <p className="col-11" style={{direction:"rtl"}}>خدمات ما</p>
                        <div className="col-1" style={{borderRadius:3,background:'#F44336',height:20,float:"right",width:'2%'}}>

                        </div>

                    </div>

                    <div className="row">
                        <CategoryList products={this.state.category} />

                    </div>

                    <div style={{height:50}}></div>

                </div>
            </Fragment>

        );
    }
}

const style={
    header:{
        // border:'2px solid red',
        height:150,
        width:'100%',
        backgroundImage:`url('https://zobs.ir/zoobs/l_m_includes/pic/main_page_background2.jpg')`,
        borderBottomRightRadius:'10%',
        backgroundSize: '100% 200px',
        paddingTop:20,
    },
    search:{
        // border:'2px solid red',
        // paddingRight:20,
        margin:0,
        marginLeft:'3%',
        borderRadius: 40,
        background: 'white',
        padding: '5px 0px 10px 10px',
        height: 50,
    },borderBlue:{
        border:'2px solid blue',
    },searchBox:{
        padding:'10px 0px 10px 10px',
    },searchIcon:{
        borderRadius:'50%',
        // height: 50,
        // padding: 10,
        marginTop: 0,
        width:"13%",
        marginLeft: "3%"
    },searchIconTop:{
        padding: 0,
    }

}
