import React from "react";
import { LottieAnimation } from '../components/lottie';
import NetworkErrorLottie from '../animation/cat_net_err.json';

export function NetworkError(){
    return <center style={{marginTop:'20%'}}>
        <LottieAnimation lotti={NetworkErrorLottie} height={200} width={200} />
        در دریافت اطلاعات خطایی رخ داد
    </center>;
}