import React,{Fragment} from 'react';
import Back from "../images/back.svg";
import User from "../images/gold-ingots.svg";
import { GetSubscription } from '../components/subscription';
import {productService} from "./ProductSerivces";
import {NetworkError} from "./networkerror";
import {Loading} from "./loading";

export class  Subscription extends React.Component {
    state = {data: [],error:0};
    async componentDidMount() {

        const {data} =await productService.getSubscription();

        this.setState({data});
    }

    render() {
        const data = this.state.data;
        if (this.state.error === 1){
            return <NetworkError />
        }
        if (data.length <1) {
            return <Loading />
        }
        else {
            return <Fragment>
                <div className="app_header">
                    <div className="back_svg" onClick={back_history}>
                        <img width="100%" src={Back}/>
                    </div>
                    <div className="back_title">
                        اشتراک ها
                    </div>
                </div>

                <div className="login_image" style={{marginTop: '20%'}}>
                    <img className="sub_image" src={User}/>

                    <div style={{marginTop: '10%', width: '100%'}}>

                        <GetSubscription data={this.state.data}/>

                    </div>

                </div>
            </Fragment>
        }

    }
}
function back_history(){
    window.history.back();
}